.profile {
  width: 100%;
}

.profileInner {
  width: 100%;
}

.profileImg {
  width: 100%;
  height: 40px;
  object-fit: cover;
  margin-bottom: 20px;
  border-radius: 6px;
}

@media screen and (max-width: 768px) {
  .profileImg {
    height: 20px;
    margin-bottom: 10px;
  }
}