.form {
  padding: 20px;
  width: 600px;
  margin: 0 auto;
  background-color: #fff;
  border: 1px solid lightgray;
  border-radius: 10px;
}

.subtitle {
  font-size: 20px;
  color: rgb(128, 126, 126, .7);
  letter-spacing: 1px;
  margin-bottom: 30px;
}

.formInner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.formInnerCheckbox {
  margin-bottom: 20px;
}

.formInnerTextarea {
  margin-bottom: 40px;
}

.label {
  margin-right: 18px;
}

.formInner p {
  width: 26%;
}

.input,
.textarea {
  width: 74%;
  padding: 6px;
  border: 1px solid lightgray;
  border-radius: 6px;
  font-size: 16px;
}

.input:focus,
.textarea:focus {
  outline: 1px solid rgba(128, 126, 126, .7);
}

.textarea {
  height: 60px;
  resize: none;
}

.btnInner {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

.button {
  padding: 6px 24px;
  color: #fff;
  font-size: 18px;
  background-color: #9ACD32;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color .2s ease;
}

.button:hover {
  background-color: #4f6919;
}

.button:disabled {
  background-color: lightgray;
  cursor: default;
}

@media screen and (max-width: 576px) {
  .form {
    width: 96%;
  }

  .formInner p {
    width: 35%;
  }

  .subtitle {
    margin-bottom: 16px;
  }

  .input,
  .textarea {
    width: 65%;
  }

  .formInnerTextarea {
    margin-bottom: 20px;
  }


  .button {
    width: 35%;
  }
}

@media screen and (max-width: 480px) {

  .formInner {
    flex-direction: column;
  }

  .formInner p {
    width: 100%;
    padding-left: 6px;
    margin-bottom: 2px;
    opacity: .7;
  }

  .input,
  .textarea {
    width: 100%;
  }

  .button {
    width: 60%;
  }
}